@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap);
:root {
  --primary-color: #f44336;
  --light-primary-color: #ffcdd2;
  --dark-primary-color: #d32f2f;
  --background: #ffcdd2;
  --text-color: #ffffff;
  --primary-text-color: ##212121;
  --seconary-text-color: #757575;
  --recipe-margin: 10% 0;
  --border-radius: 5%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Serif", serif;
  overflow-wrap: break-word;
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: var(--primary-text-color);
}

.header {
  width: 100%;
  background-color: var(--primary-color);
  text-align: center;
  padding: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.header h1 {
  margin-bottom: 3%;
  color: var(--text-color);
}

.header form {
  width: 50vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.header input {
  flex-grow: 2;
}
.header button {
  flex-grow: 1;
}

.header input,
.header button {
  padding: 2%;
  border: none;
}

.errormsg {
  width: 50vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: stretch;
  margin: 20px;
  border: 5px solid var(--dark-primary-color);
  background-color: white;
  padding: 5%;
  font-size: 1em;
  text-align: center;
}

.recipeList {
  width: 80vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: stretch;
}

@media (max-device-width: 700px) {
  .header {
    font-size: 3em;
    padding: 20% 10%;
  }

  .header form {
    width: 90%;
  }

  .header input,
  .header button {
    font-size: 1em;
  }

  .errormsg {
    font-size: 3em;
    width: 80vw;
  }

  .recipeList {
    width: 100%;
  }
}

.recipe {
  width: 300px;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 20px;
  box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.39);
  background-color: white;
}

h2 {
  text-align: center;
}

h3 {
  margin-top: 10%;
}

a {
  text-decoration: none;
  text-align: center;
}

img {
  width: 100%;
  display: block;
}

.info {
  padding: 15%;
  font-size: 1em;
  display: flex;
  flex-direction: column;
}

.nutriInfo {
  font-style: italic;
  color: var(--dark-primary-color);
  border: 1px solid #ffffff;
  transition: all 500ms ease;
}

.nutriInfo:active,
.nutriInfo:visited {
  color: var(--dark-primary-color);
}

.nutriInfo:hover {
  border: 1px solid;
}

.cookInfo {
  text-align: center;
  display: flex;
  align-items: stretch;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  margin: var(--recipe-margin);
}

.cookInfo span {
  border: 1px solid;
  padding: 3%;
}

.diet {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.diet span {
  padding: 10px;
  margin-bottom: 5%;
  text-align: center;
  background-color: var(--light-primary-color);
  border-radius: var(--border-radius);
  font-size: 0.8em;
}

.ingredientList {
  margin: var(--recipe-margin);
}

.btn {
  padding: 10px;
  background-color: var(--primary-color);
  border-radius: var(--border-radius);
  color: var(--text-color);
  transition: all 500ms ease;
}

.btn a:active,
.btn a:visited {
  color: var(--text-color);
}

.btn:hover {
  background: var(--dark-primary-color);
}

.health {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 0.8em;
  margin: var(--recipe-margin);
  color: var(--seconary-text-color);
}

.health span {
  border: 1px solid var(--seconary-text-color);
  border-radius: var(--border-radius);
  padding: 3%;
  margin-bottom: 5%;
}

@media (max-device-width: 700px) {
  .recipe {
    width: 90vw;
    font-size: 3em;
    margin: var(--recipe-margin);
    box-shadow: 0px 0px 40px 2px rgba(0, 0, 0, 0.39);
  }
}

